var render = function render(){var _vm=this,_c=_vm._self._c;return _c('v-container',{staticStyle:{"margin-bottom":"15px"},attrs:{"grid-list-lg":"","id":"gdrp","align":"center"}},[_c('v-layout',{attrs:{"col":"","wrap":""}},[_c('v-flex',{attrs:{"xs12":"","sm12":"","md12":""}},[_c('v-card',{attrs:{"elevation":"4","align":"center","color":"secondary"}},[_c('v-card-title',{staticClass:"text-md-h4 text-sm-h6 font-weight-black primary--text text-xs-center my-1"},[_vm._v(" Политика за защита, поверителност и неприкосновеност на личните данни на Пратка.БГ ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" На 25 май 2018 година за всички страни-членки на ЕС влиза в сила Общия регламент за защитата и сигурността на личните данни (General Data Protection Regulation – „GDPR“). Той се прилага пряко и в България както всеки национален закон и обхваща правата и задълженията на администраторите, обработващите и субектите на лични данни; някои от детайлите се уреждат допълнително и от българския Закон за защита на личните данни. Всички посетители на нашия уебсайт са субекти на лични данни и имат различни права в тази връзка. Тази политика има за цел да разкаже повече за принципите на защита и личните данни на посетителите на сайта ни, за нашите задължения като администратор на лични данни и за мерките, които сме предприели, за да не допуснем данните ви да бъдат използвани без да знаете от кого и с какви цели или без да имате възможност да възразите или да оттеглите съгласието си относно тяхната обработка. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Тази политика е одобрена от управителя на „Пратка.бг“ ЕООД („Пратка.бг“) и обхваща всички дейности, свързани с обработката на лични данни в рамките на дигиталните активи – собственост на Пратка.бг. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Лични данни“ – това е всяка информация, която се отнася до вас – посетителя/ползвателя на нашия уебсайт или софтуерно приложение и която самостоятелно или в съчетание с друга информация може да ни помогне да установим вашата самоличност или да свържем вашето потребителско поведение с точно определено устройство, от което достъпвате уебсайта ни, например. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Субект на личните данни“ – това сте вие, посетителят на нашия сайт или ползвателят на нашето приложение. Написаното в тази политика се отнася само за хора, физически лица, освен, ако изрично не е указано нещо различно. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Обработване на лични данни“ – това е всяко действие, което извършваме или можем да извършим с вашите лични данни, включително, но не само, тяхното събиране, анализ или унищожаване. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Администратор на лични данни“ – по отношение на нашия уебсайт/софтуерно приложение това сме ние, Пратка.бг. Ние определяме целта на обработката на вашите данни, на едно от предвидените в закона основания за това; в основни линии, ние определяме и средствата, с които се извършва тази обработка – например, техническата инфраструктура и приложения, с които се осъществява обработката. Задълженията по отношение на сигурността и защитата на вашите лични данни възникват за нас. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Обработващ лични данни“ – това е трето лице, което обработва ваши лични данни по наше възлагане, при което Пратка.бг стриктно е определил целта на обработката, средствата, с която се случва тя и е проверил дали лицето отговаря на изискванията на GDPR. Такъв обработващ може да бъде например агенция, която отговаря за маркетингова кампания на Пратка.бг в социална мрежа и създава доклади за нейната успеваемост. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Бисквитка“ е малък по обем текст или данни, които се подават на устройството (компютъра, лаптопа, таблета, телефона) за съхранение и могат да бъдат поискани обратно от подателя. Функцията на бисквитката може да е най-разнообразна: от поддържането ви в състояние „логнат“, докато попълвате определена регистрационна форма, през запаметяване на езиковите ви предпочитания за разглеждане на нашия уебсайт, до проследяване на вашето поведение при сърфиране в интернет, понякога за продължителен период от време и независимо от използваното устройство: в последния случай, както и във всички други подобни случаи, такава бисквитка не може да бъде използвана без вашето предварително съгласие за това на база на получена подробна информация. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" GDPR въвежда или затвърждава съществени правни механизми в полза на субектите на данни. По-долу сме посочили някои от тях, които имат отношение към обработката на лични данни по повод използването на дигитални активи. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Дигитални активи“ – уебсайта www.pratkabg.com ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Потребителска сесия“ – Времето между оторизацията на потребителя и момента на неговото излизане или изтичане поради неактивност на същия. През това време системата на Пратка.бг разпознава еднозначно клиента. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Plug-in“ – софтуер на трета страна, внедрен в дигитален актив на Пратка.бг и имащ отношение към проследяване на потребителско поведение и/или осигуряване работата на определена функционалност чрез преминаване към домейна на третата страна. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" „Услуги на информационното общество“ са услуги, които обикновено се предоставят срещу заплащане и от разстояние чрез използването на електронни средства. Получателят на такава услуга трябва да я е поискал изрично. Електронната търговия също е вид такава услуга. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Пратка.бг е призвана да защити неприкосновеността и да гарантира сигурността на личните данни на посетителите и потребителите на своите дигитални активи. Когато посетите нашия уебсайт, уеб сървърите на трети лица, които сме допуснали на нашия сайт (например Google), временно ще запазят данните за свързаността на вашето устройство с нашия уебсайт, както и посетените от вас страници на сайта ни, идентификационни данни за вида на вашия браузър и операционна система, както и за уебсайта, от който сте бил препратен към www.Пратка.бг. Ние не събираме и не обработваме други лични данни за вас – например вашето име, адрес, телефонен номер или E-mail адрес, освен ако не ги предоставите във връзка с ваше конкретно информационно искане, при попълване на регистрационна форма за дадена услуга или с оглед създаване на профил във виртуалния кабинет на клиента. В допълнение www.Пратка.бг изискват регистрация и парола за достъп с цел по-лесна идентификация и защита на вашите лични данни. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_c('strong',[_vm._v("За какво Пратка.БГ ползва вашите лични данни")])]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Предоставяне на поискани от вас услуги ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Почти всички лични данни, които попълвате в регистрационните форми или вашия профил са необходими, за да отговорим на ваша нужда или поискана от вас услуга, която Пратка.бг предоставя. За да предоставяме качествено услугите си онлайн и офлайн, ние се нуждаем от ваши идентификационни данни, като техният обхват варира в зависимост от вида на исканата услуга и приложимите към нея законови изисквания. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_c('strong',[_vm._v("Усъвършенстване на нашите услуги")])]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Ние бихме искали винаги да ви предлагаме най-доброто преживяване при предоставяне на нашите услуги. За тази цел можем да ви приканваме да попълвате анкети за Вашата удовлетвореност от услугите на Пратка.бг или да провеждаме директно, или с помощта на партньори, пазарни проучвания и изследвания. Вие можете да откажете участие в анкети по лесен и удобен начин на това използване и ние ще го прекратим незабавно; информацията как да оттеглите веднъж дадено съгласие или да възразите при получаване на запитване за попълване на анкета ще бъде поставена винаги на видно място в запитването. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_c('strong',[_vm._v("Маркетингова дейност")])]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Личните данни, предоставяни от вас за целите на създаване на профил, както и в други регистрационни форми може да бъде използвана, за да ви информираме за нови услуги, предоставяни от Пратка.бг, налични отстъпки на съществуващи услуги или някои улеснения в обслужването, които сме създали за вас. Изпращаните от нас съобщения ще бъдат съобразени с приложимото българско и ЕС законодателство. Вие можете да възразите по лесен и удобен начин на това използване и ние ще го прекратим незабавно; информацията как да оттеглите веднъж дадено съгласие или да възразите при получаване на нежелано маркетингово съобщение ще бъде поставена винаги на видно място в съобщението. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_c('strong',[_vm._v("Анализ на посещаемостта и проследяване на потребителското поведение на уебсайта чрез кодове от домейни на трети страни")])]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Google Analytics, Google Adwords, Google Tag Manager ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Ние използваме възможностите, предоставени ни от кодовете на Google, за да измерваме посещаемостта на сайта ни като брой и честота на посещенията. Ние не използваме тези възможности, за да събираме лични данни или индивидуални IP адреси; данните достигат до нас в обобщен вид и анонимизирани със статистическа цел и подобряване на посетителското изживяване на нашия уебсайт. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Въпреки това, събраните по този начин данни са достъпни за Google и според техните политики и условия могат да бъдат комбинирани с други данни, които Google вече има относно вашата самоличност или вашето устройство, поведението ви в дигитална среда, предпочитанията ви при търсене. Комбинираните от Google лични данни, включително събраните чрез дигиталните активи на Пратка.бг, могат да се използват от търсачката за различни цели. По тази причина, данни към Google Analytics ще бъдат подавани само с вашето предварително съгласие, което може впоследствие да бъде оттеглено по всяко време. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Интеграцията с Google Plus позволява препопулирането на информация от Google Plus за попълване на формата с данни от профила на потребителския профил на нашия сайт. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Интеграция със социалната мрежа Facebook (Използване на социален plug-in за връзка към страницата на Пратка.бг във Facebook) ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" С цел по-добрата видимост на услугите и новостите, които предоставяме на нашите клиенти, ние сме избрали да поставим на уебсайта си plug-in, който осигурява връзка към страницата ни във Facebook. Моля да имате предвид, че връзката се осъществява чрез прикачване на бисквитка от домейна на Facebook към вашето устройство, чрез която социалната мрежа може да проследява потребителското ви поведение и навиците ви на сърфиране, включително и ако не сте регистриран потребител на мрежата към момента на закачането на бисквитката. Бисквитката се поставя само с вашето предварително съгласие, което може да бъде оттеглено по всяко време. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Интегриран е Plug-in на нашия сайт, който позволява пренасочването към страницата на Пратка.бг в социалната мрежа. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Addtoany – Пратка.бг използва вградени бутони за споделяне, осигурени от посочения доставчик. Това улеснява посетителите при споделянето на съдържание в предпочитаните от тях социални мрежи. При кликването на такъв бутон, към вашето устройство ще се закачи бисквитка. Addtoany e заявила намерението си да съобрази дейността си с GDPR към 25 май 2018 година, което означава, че при поискване от ваша страна, платформата може да се съобрази с правата ви по GDPR. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Според информация от сайта на платформата, тя прави интеграция с Google Analytics, което на свой ред означава, че като краен ефект използването на бутоните за споделяне може да доведе до обработка на лични данни за вас от страна на трето лице, различно от Пратка.бг – например Google. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Други кодове, в това число бисквитки, които използваме в нашите дигитални активи ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" От една страна, използваме кодове, наречени „сесийни“, които имат „живот“ само в рамките на една потребителска сесия. Също използваме „задържащи се бисквитки“ с цел запазване на информация относно посетители, които достъпват една или повече от интернет страниците ни. Целта при ползване на бисквитките е да ви предложим оптимално потребителско изживяване и да ви „разпознаваме“, като предлагаме на вниманието ви, доколкото е възможно, различни интернет страници и ново съдържание при завръщането ви на нашия уебсайт. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"left","justify":"center"}},[_vm._v(" Ние не създаваме като правило индивидуален профил на вашите онлайн дейности. Съдържанието на прикрепващата се бисквитка се изчерпва с идентификационен номер. Бисквитките не се свързват с име, E-mail адрес, IP адрес. ")]),_c('v-card-text',{staticClass:"font-weight-bold",attrs:{"align":"center","justify":"center"}},[_c('strong',[_vm._v("Права на субекта на личните данни")]),_c('br')]),_c('v-card-text',{staticClass:"font-weight-regular",attrs:{"align":"left","justify":"center"}},[_c('ol',[_c('li',[_c('strong',[_vm._v("Право на информация")]),_c('br'),_c('p',[_vm._v(" Вие имате право да получите информация за обработката на вашите лични данни, включително целта, срока и основанието, с което те се събират, данни идентифициращи администратора на лични данни, както и негови координати за връзка и координати за връзка с длъжностно лице по защита на данните. ")])]),_c('li',[_c('strong',[_vm._v("Право на достъп")]),_c('br'),_c('p',[_vm._v(" Вие имате право да достъпите личните си да