<template>
         <v-container>
            <v-layout align-center justify-center>
               <v-col cols="12" md="6" lg="6" xl="6">
                  <v-card class="elevation-10">
                     <v-img gradient='90deg, rgba(251,213,61,1) 0%, rgba(250,158,0,1) 20%, rgba(215,0,2,1) 100%'>
                        <v-toolbar dark color="transparent">
                           <v-toolbar-title class="black--text">
                              {{menu_title}}
                           </v-toolbar-title>
                        </v-toolbar>
                     </v-img>

                     <v-card-text>
                        <v-spacer></v-spacer>
                        <v-form ref="form" @submit.prevent="">
                           <v-row>
                              <v-col cols="12" xs="12" sm="12" md="12" lg="12" xl="12">
                                 <v-row>
                                    <v-card-text v-if="can_recover_password">
                                       Възтановяване на потребителска парола
                                    </v-card-text>
                                 </v-row>

                                 <v-row v-if="can_recover_password">
                                    <v-col>
                                        <v-text-field
                                          :rules="passwordValidationRules"
                                          v-model="password"
                                          name="password"
                                          label="Password"
                                          type="password"
                                          placeholder="Password"
                                          required
                                          ></v-text-field>
                                    </v-col>

                                    <v-col>
                                       <v-text-field
                                       v-model="confirmPassword"
                                       :rules="[ v => v==password || 'Паролата не отговаря на въведената!']"
                                       name="confirmPassword"
                                       label="Confirm Password"
                                       type="password"
                                       placeholder="Confirm Password"
                                       required
                                       ></v-text-field>
                                    </v-col>
                                 </v-row>

                                 <v-row>
                                    <v-col>
                                       <v-expand-x-transition ease-in>
                                             <v-layout v-show='account_recovery_alert' row wrap justify-center>
                                                <v-flex xs8 sm10 md10>
                                                      <v-alert
                                                      :type="account_recovery_alert_type"
                                                      > {{ account_recovery_message }}
                                                      </v-alert>
                                                </v-flex>
                                             </v-layout>
                                       </v-expand-x-transition>
                                    </v-col>
                                 </v-row>

                                 <v-row v-if="can_recover_password">
                                    <v-col>
                                       <v-btn block rounded @click="resetPassword()" color="primary" value="passwordReset">{{password_reset_button}}</v-btn> 
                                    </v-col>
                                 </v-row>
                              </v-col>
                           </v-row>
                           
                           <v-expand-x-transition ease-in>
                              <v-layout v-show='error_status' row wrap justify-center>
                                 <v-flex xs8 sm10 md10>
                                       <v-alert
                                       type="error"
                                       dismissible
                                       > {{ error_message }}
                                       </v-alert>
                                 </v-flex>
                              </v-layout>
                           </v-expand-x-transition>

                        </v-form>
                        <v-spacer></v-spacer>
                     </v-card-text>

                  </v-card>
                
               </v-col>
            </v-layout>
         </v-container>
</template>

<script>
import axios from 'axios';
// import { mapActions } from 'vuex';

export default {
   name: 'Login',

   components: {
   },

   data() {
      return {
         error_status: false,
         error_message: '',
         password: "",
         confirmPassword: "",

         can_recover_password: false,
         account_recovery_alert: false,
         account_recovery_alert_type: '',
         account_recovery_message: '',

         errorMessage: "",
         menu_title: "Смяна на парола",
         password_reset_button: "Смяна на парола",

         passwordValidationRules:[
            v => !!v || 'Полето е задължително',
            v => /([A-Za-z0-9])\w/.test(v) || 'Паролата трябва да е на латиница',
            v => /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/.test(v) || 'Паролата трябва да е поне 6 символа, като съдържа минимум 1 буква и 1 число.',
         ],
      };
   },

    computed: {
        recover_password() {
            return this.$attrs.recover_password
        },
        user_email() {
            return this.$attrs.user_email
        },
   },


   methods: {

        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        },

        confirmLeave() {
            return window.confirm('Не сте сменили парола! Ако излезнете сега, паролата Ви няма да бъде сменена!')
        },

        confirmStayInDirtyForm() {
            return !this.confirmLeave()
        },

        beforeWindowUnload(e) {
            if (this.confirmStayInDirtyForm()) {
                // Cancel the event
                e.preventDefault()
                // Chrome requires returnValue to be set
                e.returnValue = ''
            }   
        },

      async checkPasswordResettable() {
        let payload = {
            "email": this.$attrs.user_email,
            "recovery_link": this.$attrs.recover_password,
        }
        // console.log(payload)
        let response = await axios.post('/login/check-recovery', payload)
        if(response.data == 'passwordCanBeUpdated'){
            this.can_recover_password = true
        }
        else{
            this.account_recovery_alert = true
            this.account_recovery_alert_type = 'error'
            this.account_recovery_message = "Линкът вече не е валиден. Моля направете нова заявка за смяна на парола."
        }
        // console.log(response)
      },

      async resetPassword() {
         let payload = {
            "email": this.$attrs.user_email,
            "recovery_link": this.$attrs.recover_password,
            "password": this.confirmPassword,
         }
         let response = await axios.post('/login/recovery', payload)
         // console.log(response)
         if(response.data == 'timeRunOut'){
            this.account_recovery_alert = true
            this.account_recovery_alert_type = 'warning'
            this.account_recovery_message = "Линкът вече не е валиден. Моля направете нова заявка за смяна на парола."

            await this.sleep(3000)
            this.$router.push('/login');
         }
         else if(response.data == 'incorrectData'){
            this.account_recovery_alert = true
            this.account_recovery_alert_type = 'error'
            this.account_recovery_message = "Невалиден линк или имейл на потребител."

            await this.sleep(3000)
            this.$router.push('/login');
         }
         else if(response.data == 'success'){
            this.account_recovery_alert = true
            this.account_recovery_alert_type = 'success'
            this.account_recovery_message = "Паролата е успешно сменена. Ще бъдете пренасочени обратно към страницата за вход."

            await this.sleep(3000)
            this.$router.push('/login');
         }
      },
   },

   beforeRouteLeave (to, from, next) {
        // If the form is dirty and the user did not confirm leave,
        // prevent losing unsaved changes by canceling navigation
        if (this.confirmStayInDirtyForm()){
            next(false)
            window.location = to.path
        } else {
            // Navigate to next view
            next()
        }
    },

   created() {
        window.addEventListener('beforeunload', this.beforeWindowUnload)
    },

    mounted() {
        console.log(this)

        this.checkPasswordResettable()
    },

    beforeDestroy() {
        window.removeEventListener('beforeunload', this.beforeWindowUnload)
    },
}
</script>
