<template>
    <v-container id='complete_order' align='center'>
        <v-card elevation="4" color='secondary'>
            <v-card-title>
                <v-app-bar 
                elevation="2"
                color="primary"
                dark
                rounded
                align='center' >
                Завърши поръчка
                </v-app-bar>
            </v-card-title>

            <v-container>
                <v-row fixed align='center' justify='center'>
                    <v-col cols='12' xs='12' sm='12' md='8'>
                        <v-hover v-slot="{ hover }">
                            <v-card
                            :class="`elevation-${hover ? 24 : 2}`"
                            class="mx-auto pa-6 transition-swing"
                            elevation="4" tile outlined color='secondary'>
                                <v-card-title class="text-h5">Пратки</v-card-title>
                                <v-container>
                                    <v-row v-if="parcel_container">
                                        <v-col cols="12" v-for="item in parcel_container"
                                            :key="item.internalID">
                                            <v-card color="white">
                                                <v-list-item three-line>
                                                    <v-list-item-content>
                                                        <div class="text-overline mb-4">
                                                        Парньор: {{item.service_provider}}
                                                        </div>
                                                        <v-list-item-title class="text-h6 mb-1">
                                                        {{item.shipmentDescription}} - {{reverse_typeOfDelivery(item.deliveryType)}},  Тегло: {{item.parcelWeight}}кг
                                                        </v-list-item-title>
                                                        <v-list-item-subtitle class="info--text text-subtitle-1 font-weight-bold mb-1">Цена: {{item.shipmentPrice}} .лв</v-list-item-subtitle>
                                                    </v-list-item-content>

                                                    <!-- Delete Item  -->
                                                    <v-list-item-avatar
                                                    tile
                                                    size="40"
                                                    >
                                                        <v-icon
                                                        small
                                                        color="primary"
                                                        @click="deleteItem(item)"
                                                        >
                                                            mdi-delete
                                                        </v-icon>
                                                    </v-list-item-avatar>
                                                </v-list-item>
                                            </v-card>
                                        </v-col>
                                    </v-row>
                                    <v-row v-else>
                                        <v-col>
                                            <v-card color="white">
                                                <v-card-subtitle>Нямате пратки все още...</v-card-subtitle>
                                            </v-card>
                                        </v-col>
                                    </v-row>
                                    <v-row align="center" justify="center">
                                        <v-col class="text-subtitle-1 font-weight-bold">
                                            Обща цена:
                                        </v-col>
                                        <v-spacer></v-spacer>
                                        <v-col cols="2" class="text-subtitle-1 font-weight-bold" align="center" justify="center">
                                            <v-text-field
                                            readonly
                                            solo
                                            dense
                                            :value="total_price_order+' .лв'"
                                            >
                                            </v-text-field>
                                        </v-col>
                                    </v-row>
                                </v-container>

                            </v-card>
                        </v-hover>
                    </v-col>

                    <v-col cols='12' xs='12' sm='12' md='4'>
                        <v-hover v-slot="{ hover }">
                            <v-card height="250"
                            :class="`elevation-${hover ? 24 : 2}`"
                            class="mx-auto pa-6 transition-swing"
                            elevation="4" tile outlined color='secondary'>
                                <v-card-title>При въпроси</v-card-title>
                                <v-card-subtitle>ПРАТКА.БГ е тук за теб</v-card-subtitle>
                                <v-container>
                                    <v-row>
                                        <v-col>
                                            <v-icon large>mdi-phone</v-icon><a href="tel:+359886926633">0886 926 633</a>
                                        </v-col>
                                        
                                    </v-row>
                                    <v-row>
                                        <v-col>
                                            <v-icon large>mdi-mail</v-icon><a href="mailto:info@pratkabg.com">info@pratkabg.com</a>
                                        </v-col>
                                    </v-row>
                                </v-container>

                            </v-card>
                        </v-hover>
                    </v-col>
                </v-row>

                <v-divider style="margin:20px"></v-divider>

                <v-row align='center' justify='center'>
                    <v-col align='center' justify='center'>
                        <v-btn
                        rounded
                        color="info"
                        to="/send">
                            <v-icon
                                left
                                dark
                            >
                                mdi-package-variant-closed-plus
                            </v-icon>
                        Създай нова пратка 
                        </v-btn>
                    </v-col>
                    <v-spacer></v-spacer>
                    <v-col align='center' justify='center'>
                        <v-form
                        ref="form_payment_finish"
                        @submit.prevent=""
                        @submit="checkout_shopping_cart"
                        lazy-validation
                        autocomplete='off'>
                            <v-btn
                            rounded
                            block
                            color="primary"
                            type="submit"
                            :loading="response_is_loading"
                            :disabled="response_is_loading"
                            @click="loader = 'response_is_loading'"
                            >
                            Плати и изпрати <v-icon right> mdi-credit-card-check-outline </v-icon>
                            <template v-slot:loader>
                                <span>Заявката се обработва <v-progress-circular indeterminate></v-progress-circular> </span>
                            </template>
                            </v-btn>
                        </v-form>
                    </v-col>
                </v-row>
            </v-container>


        </v-card>

        <v-dialog v-model="dialogDelete" max-width="50%">
          <v-card >
            <v-card-title class="text-h5 text-center">Сигурни ли сте, че искате да изтриете пратката?</v-card-title>
            <v-card-text>
            </v-card-text>
            <v-card-actions>
                <v-container>
                <v-row align="center" dense>
                    <v-col>
                        <v-btn block color="secondary" class="black--text" rounded @click="closeDelete">Не</v-btn>
                    </v-col>
                    <v-spacer></v-spacer>
                    <v-col>
                        <v-btn block color="primary" rounded @click="deleteItemConfirm">Да</v-btn>
                    </v-col>
                </v-row>
            </v-container>
            </v-card-actions>
          </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
// import Vue from 'vue'
// import axios from 'axios';

export default {
    name: 'ParcelCheckout',


    data: () => ({
        response_is_loading: false,
        dialogDelete: false,
        editedIndex: -1,
        editedItem: {},

        currentParcels: [{
            "parcelInvoice":null,
            "parcelType":0,
            "parcelWeight":"0.5",
            "deliveryType":0,
            "shipmentPrice":4.05,
            "shipmentDescription":"Документи",
            'service_provider': "econt",
            "sender":{
                "senderDestID":"123",
                "senderOfficeID":"252",
                "senderAddress":"Ул. Георги Трингов 6",
                "senderName":"Симеон Марлоков",
                "senderPhone":"0897669022",
                "senderEmail":"simeon.marlokov@gmai.com",
                "senderOrganization":""
            },
            "recipient":{
                "recipientDestID":"273",
                "recipientOfficeID":"268",
                "recipientAddress":"Ул. Градинарска 7",
                "recipientName":"Ivan Ivanov",
                "recipientPhone":"0897113322",
                "recipientEmail":"",
                "recipientOrganization":""
            },
            "promoCode":""
            }],
    }),

    watch: {

    },

    computed: {
        total_price_order() {
            let total_price = 0.00
            this.parcel_container.forEach(element => {
                // console.log(element)
                if(element.shipmentPrice){
                    total_price += element.shipmentPrice
                }
                
            });
            // console.log(total_price)
            return total_price
        },

        parcel_container() {
            return this.$store.getters.parcelsUser
        },
    },

    methods: {
        reverse_typeOfDelivery(delivery_type) {
            let service_name = ''

            if(delivery_type == 0){
                service_name = 'Офис до офис'
            }
            
            else if(delivery_type == 1){
                service_name = 'Офис до адрес'
            }

            else if(delivery_type == 2){
                service_name = 'Адрес до офис'
            }

            else if(delivery_type == 3){
                service_name = 'Адрес до адрес'
            }
            return service_name
        },
        reverse_typeOfParcel(parcel_type) {
            let service_name = ''

            if(parcel_type == 0){
                service_name = 'Документи'
            }
            
            else if(parcel_type == 1){
                service_name = 'Колет'
            }

            else if(parcel_type == 2){
                service_name = 'Адрес до офис'
            }

            else if(parcel_type == 3){
                service_name = 'Адрес до адрес'
            }
            return service_name
        },

        // Operations
        deleteItem (item) {
            this.editedIndex = this.parcel_container.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialogDelete = true
        },

        deleteItemConfirm () {
            this.closeDelete()
        },

        closeDelete () {
            this.dialogDelete = false
            this.$nextTick(() => {
                this.editedItem = {}
                this.editedIndex = -1
            })
        },

        // PAYMENT METHOD
        async checkout_shopping_cart() {
            console.log('checkout!')
        },
    },

    // beforeRouteLeave (to, from, next) {

    // },

    created() {

    },

    mounted() {

    },

    beforeDestroy() {

    },
    
};
</script>

