<template>
         <v-container>
            <v-layout align-center justify-center>
               <v-col cols="12" md="10" lg="10" xl="10">
                  <v-card class="elevation-10">
                     <v-img gradient='90deg, rgba(251,213,61,1) 0%, rgba(250,158,0,1) 20%, rgba(215,0,2,1) 100%'>
                        <v-toolbar dark color="transparent">
                           <v-toolbar-title class="black--text">
                              {{menu_title}}
                           </v-toolbar-title>
                        </v-toolbar>
                     </v-img>

                     <v-card-text v-if="flag_login">
                        <v-spacer></v-spacer>
                        <v-form ref="login_form" 
                        @submit.prevent=""
                        @submit="submitLogin()"
                        v-model="valid_login_form"
                        lazy-validation>
                           
                           <v-row>
                              <v-col class='d-none d-sm-block' cols="6" sm="6" md="6" lg="5" xl="4" >
                                 <v-img contain max-height="200" max-width="300"
                                 src='../../assets/images/d1.jpg'/>
                              </v-col>

                              <v-col cols="12" xs="12" sm="6" md="6" lg="7" xl="8">
                                 <v-row>
                                    <v-col>
                                       <v-text-field
                                       v-model="email"
                                       name="email"
                                       label="Email"
                                       type="text"
                                       placeholder="Email"
                                       :rules="emptyValidationRule"
                                       required
                                       ></v-text-field>
                                    </v-col>
                                 </v-row>

                                 <v-row>
                                    <v-col>
                                       <v-text-field
                                       v-model="password"
                                       name="password"
                                       label="Password"
                                       type="password"
                                       placeholder="Password"
                                       :rules="emptyValidationRule"
                                       required
                                       ></v-text-field>
                                    </v-col>
                                 </v-row>
                                 <v-row>
                                    <v-col>
                                       <v-btn block rounded @click="forgottenPassword()" color="primary">{{password_reset_button}}</v-btn> 
                                    </v-col>
                                    <v-col>
                                       <v-btn :loading="isLoadingStatus" :disabled="isLoadingStatus" block rounded type="submit" color="primary">
                                          {{isRegister ? stateObj.register.name : stateObj.login.name}}
                                       </v-btn>
                                    </v-col>
                                 </v-row>

                                 <v-row>
                                    <v-col>
                                       <v-btn block rounded class="info" @click="switchLogin()">
                                          {{toggleMessage}}  
                                       </v-btn>
                                    </v-col>
                                 </v-row>
                              </v-col>
                           </v-row>

                           <v-row v-if="isRegister">
                              <v-text-field 
                              v-model="confirmPassword"
                              name="confirmPassword"
                              label="Confirm Password"
                              type="password"
                              placeholder="Confirm Password"
                              required
                              ></v-text-field>
                           </v-row>

                           <v-col v-if="isRegister">
                              <v-row>
                                 <v-text-field
                                 v-model="firstName"
                                 name="firstName"
                                 label="First Name"
                                 type="text"
                                 placeholder="Your given name"
                                 required
                                 ></v-text-field>
                              </v-row>
                              <v-row>
                                 <v-text-field 
                                 v-model="lastName"
                                 name="lastName"
                                 label="Last Name"
                                 type="text"
                                 placeholder="Your family name"
                                 required
                                 ></v-text-field>
                              </v-row>
                              <v-row>
                                 <v-text-field 
                                 v-model="phone_number"
                                 name="phone_number"
                                 label="Phone Number"
                                 type="text"
                                 placeholder="Your phone number"
                                 required
                                 ></v-text-field>
                              </v-row>
                           </v-col>

                        </v-form>
                     </v-card-text>

                     <v-card-text>
                        <v-row>
                        <v-col>
                           <v-expand-x-transition ease-in>
                              <v-layout v-show='alarm_status' row wrap justify-center>
                                 <v-flex xs8 sm10 md10>
                                       <v-alert
                                       type="error"
                                       > {{ alarm_message }}
                                       </v-alert>
                                 </v-flex>
                              </v-layout>
                           </v-expand-x-transition>
                        </v-col>
                     </v-row>
                     </v-card-text>

                     <v-card-text v-if="flag_forgotten_password">
                        <v-spacer></v-spacer>
                        <v-form
                        ref="form"
                        @submit.prevent=""
                    >
                           
                           <v-row>
                              <!-- <v-col class='d-none d-sm-block' cols="6" sm="6" md="6" lg="5" xl="4" >
                                 <v-img contain max-height="200" max-width="300"
                                 src='../../assets/images/d1.jpg'/>
                              </v-col> -->

                              <v-col cols="12" xs="12" sm="12" md="12" lg="12" xl="12">
                                 <v-row>
                                    <v-card-text>
                                       Моля въведете ел. поща, използвна за акаунта. При наличие на адресът в базата данни, ще получите в пощата линк за смяна на паролата.
                                    </v-card-text>
                                 </v-row>
                                 <v-row>
                                    <v-col>
                                       <v-text-field
                                       v-model="email"
                                       name="email"
                                       label="Email"
                                       type="text"
                                       placeholder="Email"
                                       required
                                       ></v-text-field>
                                    </v-col>
                                 </v-row>

                                 <v-row>
                                    <v-col>
                                       <v-expand-x-transition ease-in>
                                             <v-layout v-show="account_recovery_alert" row wrap justify-center>
                                                <v-flex xs8 sm10 md10>
                                                      <v-alert
                                                      :type="account_recovery_alert_type"
                                                      > {{ account_recovery_message }}
                                                      <v-progress-circular v-if="account_recovery_alert_type == 'info'"
                                                         indeterminate
                                                         color="primary"
                                                      ></v-progress-circular>
                                                      </v-alert>
                                                </v-flex>
                                             </v-layout>
                                       </v-expand-x-transition>
                                    </v-col>
                                 </v-row>

                                 <v-row>
                                    <v-col>
                                       <v-btn block rounded @click="resetPassword()" color="primary" value="passwordReset">{{password_reset_button}}</v-btn> 
                                    </v-col>
                                 </v-row>
                                 <v-row>
                                    <v-col>
                                       <v-btn block outlined @click="backToLogin()" rounded color="primary" value="toLogin">{{back_to_login}}</v-btn> 
                                    </v-col>
                                 </v-row>
                              </v-col>
                           </v-row>
                           
                           <v-expand-x-transition ease-in>
                              <v-layout v-show='alarm_status' row wrap justify-center>
                                 <v-flex xs8 sm10 md10>
                                       <v-alert
                                       type="error"
                                       dismissible
                                       > {{ alarm_message }}
                                       </v-alert>
                                 </v-flex>
                              </v-layout>
                           </v-expand-x-transition>

                        </v-form>
                        <v-spacer></v-spacer>
                     </v-card-text>

                     <v-card-text v-if="flag_register">
                           
                           <v-row>
                              <v-col cols="12" xs="12" sm="12" md="12" lg="12" xl="12">
                                 <v-form ref="register_form" @submit.prevent=""
                                 @submit="register()"
                                 v-model="valid_register_form"
                                 lazy-validation>
                                    <v-row>
                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                          :rules="nameValidationRule"
                                          v-model="firstName"
                                          label="Име"
                                          type="text"
                                          placeholder="Иван..."
                                          required
                                          ></v-text-field>
                                       </v-col>

                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field 
                                          :rules="nameValidationRule"
                                          v-model="lastName"
                                          label="Фамилия"
                                          type="text"
                                          placeholder="Иванов..."
                                          required
                                          ></v-text-field>
                                       </v-col>

                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                          :rules="emptyValidationRule"
                                          v-model="phone_number"
                                          label="Телефон"
                                          type="number"
                                          placeholder="0896000000"
                                          required
                                          ></v-text-field>
                                       </v-col>
                                    </v-row>

                                    <v-row>
                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-select
                                             :rules="emptyValidationRule"
                                             v-model="account_type"
                                             :items="user_account_types"
                                             item-value="value"
                                             item-text="description"
                                             background-color='white darken-3'
                                             label="Тип клиентски профил"
                                             type="text"
                                             placeholder="Потребител/Организация"
                                             required
                                          ></v-select>
                                       </v-col>

                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                             :rules="emailValidationRules"
                                             v-model="email"
                                             label="Поща"
                                             type="text"
                                             placeholder="ivan.ivanov@abv.bg"
                                             required
                                          ></v-text-field>
                                       </v-col>

                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                          :rules="[ v => v==email || 'Имейлът не отговаря на въведения!']"
                                          v-model="confirmEmail"
                                          label="Потвърди поща"
                                          type="text"
                                          placeholder="ivan.ivanov@abv.bg"
                                          required
                                          ></v-text-field>
                                       </v-col>
                                    </v-row>
                                 </v-form>

                                 <v-form ref="corporate_registration" @submit.prevent=""
                                 v-model="valid_corporate_register_form"
                                 lazy-validation>
                                    <v-row v-show="account_type == 'organization'">
                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                             :rules="nameValidationRule"
                                             v-model="organization_name"
                                             label="Име на фирмата"
                                             placeholder="Firma OOD"
                                             :required="isCorporate"
                                             ></v-text-field>
                                       </v-col>

                                       <v-col cols="6" xs="2" sm="2" md="2">
                                          <v-checkbox
                                          v-model="organization_vat"
                                          label="Регистрирана по ДДС"
                                          hint="Ако сте регистрирани по ДДС, изберете тази отметка"
                                          ></v-checkbox>
                                       </v-col>

                                       <v-col cols="6" xs="10" sm="10" md="2">
                                          <v-text-field
                                          v-model="organization_eik"
                                          :rules="emptyValidationRule"
                                          label="ЕИК"
                                          placeholder="000000000"
                                          :required="isCorporate"
                                          ></v-text-field>
                                       </v-col>

                                       <v-col cols="6" xs="12" sm="12" md="4">
                                          <v-text-field
                                          v-model="organization_mol"
                                          :rules="nameValidationRule"
                                          label="МОЛ"
                                          placeholder="Иван Иванов"
                                          :required="isCorporate"
                                          ></v-text-field>
                                       </v-col>
                                    </v-row>
                                 </v-form>

                                 <v-form ref="register_form" @submit.prevent=""
                                 @submit="register()"
                                 v-model="valid_register_form"
                                 lazy-validation>
                                    <v-row>
                                       <v-col>
                                          <v-text-field
                                             :rules="passwordValidationRules"
                                             v-model="password"
                                             name="password"
                                             label="Парола"
                                             type="password"
                                             placeholder="******"
                                             required
                                             ></v-text-field>
                                       </v-col>

                                       <v-col>
                                          <v-text-field
                                          v-model="confirmPassword"
                                          :rules="[ v => v==password || 'Паролата не отговаря на въведената!']"
                                          name="confirmPassword"
                                          label="Потвърди парола"
                                          type="password"
                                          placeholder="******"
                                          required
                                          ></v-text-field>
                                       </v-col>
                                    </v-row>

                                    <v-row>
                                       <v-col>
                                       <v-checkbox
                                          required
                                          :rules="[v => !!v || 'Трябва да се съгласите с условията на ПРАТКА.БГ за да продължите.']"
                                          color='primary'
                                          style='black--text'
                                          :label="sendTermsOfUse"
                                          value="red"
                                          hide-details>
                                       </v-checkbox>
                                       </v-col>
                                    </v-row>
                                 
                                 <v-row>
                                    <v-col>
                                       <v-btn block rounded type="submit" color="primary">{{isRegister ? stateObj.register.name : stateObj.login.name}}</v-btn>
                                    </v-col>
                                 </v-row>
                                 </v-form>
                                 <v-row>
                                    <v-col>
                                        <v-btn block rounded class="info" @click="switchLogin()">
                                          {{toggleMessage}}  
                                       </v-btn>
                                    </v-col>
                                 </v-row>
                              </v-col>
                           </v-row>
                           
                           <v-expand-x-transition ease-in>
                              <v-layout v-show='alarm_status' row wrap justify-center>
                                 <v-flex xs8 sm10 md10>
                                       <v-alert
                                       :type="alarm_type"
                                       dismissible
                                       > {{ alarm_message }}
                                       </v-alert>
                                 </v-flex>
                              </v-layout>
                           </v-expand-x-transition>

                     </v-card-text>
                  </v-card>
                
               </v-col>
            </v-layout>
         </v-container>
</template>

<script>
import Vue from 'vue'
import axios from 'axios';
import { mapActions } from 'vuex';

export default {
   name: 'Login',

   metaInfo: {
      title: 'Вход |'
    },

   components: {
   },

   data() {
      return {
         alarm_status: false,
         alarm_type: 'info',
         alarm_message: '',
         email: "",
         confirmEmail: "",
         password: "",
         confirmPassword: "",
         firstName: "",
         lastName: "",
         phone_number: "",
         account_type: "",
         organization_name: "",
         organization_eik: "",
         organization_mol: "",
         organization_vat: false,
         isRegister : false,

         isLoadingStatus: false,

         flag_forgotten_password: false,
         flag_login: true,
         flag_register: false,
         back_to_login: 'Обратно към вход',
         valid_login_form: true,
         valid_register_form: true,
         valid_corporate_register_form: true,


         account_recovery_alert: false,
         account_recovery_alert_type: 'info',
         account_recovery_message: '',
         stateObj: {
            register :{
               title: 'Регистрация',
               name: 'Създай потребител',
               message: 'Върни се към вход'
            },
            login : {
               title: 'Потребителски панел',
               name: 'Вход',
               message: 'Регистрирай се'
            },
            forgotten : {
               title: 'Забравена парола',
            },
         },
         password_reset_button: "Забравена Парола",
         sendTermsOfUse: 'Съгласявам се с условията за ползване на ПРАТКА ЕООД, както и ПРАТКА ЕООД да съхранява и обработва необходимите лични данни.',

         user_account_types: [{
               'value': 'user',
               'description': 'Потребителски'
            },
            {
               'value': 'organization',
               'description': 'Фирмен'
            },
         ],

         // Validation Rules
         emptyValidationRule: [
            v => !!v || 'Полето е задължително'
         ],
         nameValidationRule:[
            v => !!v || 'Полето е задължително',
            v => /([A-Za-z])\w/.test(v) || 'Името трябва да е на латиница'
         ],
         emailValidationRules:[
            v => !!v || 'Полето е задължително',
            v => /.+@.+\..+/.test(v) || 'Невалиден имейл',
            v => /([A-Za-z])\w/.test(v) || 'Пощата трябва да е на латиница'
         ],
         passwordValidationRules:[
            v => !!v || 'Полето е задължително',
            v => /([A-Za-z0-9])\w/.test(v) || 'Паролата трябва да е на латиница',
            v => /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/.test(v) || 'Паролата трябва да е поне 6 символа, като съдържа минимум 1 буква и 1 число.',
         ],
      };
   },


   methods: {
      ...mapActions(['setData']),
      async submitLogin() {
          let is_form_ready = this.$refs.login_form.validate()
            if (is_form_ready == true) {
               this.isLoadingStatus = true
               const User = new FormData();
               User.append('username', this.email);
               User.append('password', this.password);
               Vue.analytics.fbq.event('trackCustom', {
                    content_name: 'UserLogin'
               })
               let response = await axios.post('/login', User)

               if (response.data.status == 'unauthorized') {
                  this.alarm_status = true
                  this.alarm_message = 'Грешна поща или парола'
               }

               else {
                  await this.setData();
                  if (this.$store.getters.accountUser == 'admin') {
                     this.alarm_status = false
                     this.$router.push('/admin')
                  
                  } else if (this.$store.getters.isAuthenticated) {
                     this.alarm_status = false
                     this.$router.push('/home');
                  } 
               }
               this.isLoadingStatus = false
            } else {
                void(0)
            }
      },

      async register() {
         let is_form_ready = this.$refs.register_form.validate()
         if (is_form_ready == true) {
            if (this.account_type == 'organization') {

               let is_corporate_form_ready = this.$refs.corporate_registration.validate()
               if (is_corporate_form_ready == true) {
                  let payload = {
                     'email': this.email,
                     'password': this.password,
                     'first_name': this.firstName,
                     'last_name': this.lastName,
                     'phone_number': this.phone_number,
                     'account_type': this.account_type,
                     'organization_data': {
                        'organization_name': this.organization_name,
                        'organization_vat': this.organization_vat,
                        'organization_eik': this.organization_eik,
                        'organization_mol': this.organization_mol,
                        'organization_iban': '',
                        'organization_email': '',
                     }
                  }
                  
                  let request = await axios.post('/register/user', payload)
                  Vue.analytics.fbq.event('trackCustom', {
                     content_name: 'CorporateSignUp'
                  })
                  // console.log(request)
                  if(request.data.status == 'success'){
                     this.isRegister = !this.isRegister
                     this.alarm_type = 'success'
                     this.alarm_message = "Успешно създадохте своя профил!"
                     this.alarm_status = true
                     this.flag_login = true;
                     this.flag_register = false;
                  }  else if(request.data.status == 'emailExists') {
                     this.alarm_type = 'error'
                     this.alarm_message = "Тази поща вече е регистрирана."
                     this.alarm_status = true
                  } else if(request.data.status == 'phoneExists') {
                     this.alarm_type = 'error'
                     this.alarm_message = "Този телефонен номер вече е регистриран."
                     this.alarm_status = true
                  }
               } else {
                  void(0)
               }
            }

            else {

               let payload = {
                  'email': this.email,
                  'password': this.password,
                  'first_name': this.firstName,
                  'last_name': this.lastName,
                  'phone_number': this.phone_number,
                  'account_type': this.account_type,
               }
               
               let request = await axios.post('/register/user', payload)
               Vue.analytics.fbq.event('trackCustom', {
                  content_name: 'UserSignUp'
               })
               // console.log(request)
               if(request.data.status == 'success'){
                     this.isRegister = !this.isRegister
                     this.alarm_status = true
                     this.alarm_type = 'success'
                     this.alarm_message = "Успешно създадохте своя профил!"
                     this.flag_login = true;
                     this.flag_register = false;
                  }  else if(request.data.status == 'emailExists') {
                     this.alarm_status = true
                     this.alarm_type = 'error'
                     this.alarm_message = "Тази поща вече е регистрирана."
                  } else if(request.data.status == 'phoneExists') {
                     this.alarm_status = true
                     this.alarm_type = 'error'
                     this.alarm_message = "Този телефонен номер вече е регистриран."
                  }

            }
               
         } else {
            void(0)
         }
      },

      async forgottenPassword() {
         this.flag_login = false;
         this.flag_register = false;
         this.flag_forgotten_password = true;
      },

      async backToLogin() {
         this.flag_forgotten_password = false;
         this.flag_register = false;
         this.flag_login = true;
      },

      async toRegister() {
         this.flag_forgotten_password = false;
         this.flag_login = false;
         this.flag_register = true;
      },

      async switchLogin() {
         this.isRegister = !this.isRegister
         this.flag_login = !this.flag_login
         this.flag_register = !this.flag_register
      },

      async resetPassword() {
         let payload = {
            "email": this.email,
         }
         this.account_recovery_alert = true
         this.account_recovery_alert_type = 'info'
         this.account_recovery_message = "Заявката се обработва..."
         let response = await axios.post('/login/forgotten-password', payload)
         // console.log(response)
         if(response.data == 'noEmail'){
            
            this.account_recovery_alert_type = 'error'
            this.account_recovery_message = "Такава ел. поща не съществува в базата данни."
         }
         else if(response.data == 'resetLinkCreated'){
            this.account_recovery_alert_type = 'success'
            this.account_recovery_message = "Mоля сменете паролата си от линка изпратен на е-майла ви. Проверете spam папката в случай че не е в Inbox/Получени. Линкът е валиден в следващите два часа."
         }
         else if(response.data == 'existingLink') {
            this.account_recovery_alert_type = 'warning'
            this.account_recovery_message = "Имате създаден линк за смяна на парола, който още е валиден. Моля, проверете пощата си."
         }
      },
   },

   computed: {
      toggleMessage : function() { 
         return this.isRegister ? this.stateObj.register.message : this.stateObj.login.message 
      },
      menu_title: function() { 
         if(this.flag_register) return 'Регистрация'
         else if (this.flag_login) return 'Потребителски панел'
         else return 'Забравена парола'
      },
      isCorporate() {
         if (this.account_type == 'organization') return true
         else return false
      }
   }
}
</script>
