<template>
    <v-container grid-list-lg id='parcelCreation' align='center' style='margin-bottom:15px;'>

        <!-- <v-progress-circular :size="100" color="primary" indeterminate><v-img height='50px'
                                width='50px'
                                cover
                                src="../assets/images/delivery_box.png" ></v-img></v-progress-circular> -->
        
        <v-layout row wrap>
    
            <v-flex xs12 sm12 md12>
                <!-- Parcel Start -->
                <v-card v-show='parcelIntro' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded>
                            <v-tabs 
                            color='white'
                            v-model='selectedCategory'
                            active-class='primary lighten-1'
                            icon
                            show-arrows
                            grow
                            @click="GA_SelectDestination">
                            <v-tab class='tab-text secondary--text'>България-България</v-tab>
                            <v-tab class='tab-text secondary--text lighten-2'>България-Международни</v-tab>
                            <!-- <v-tab disabled class='tab-text secondary--text lighten-2'>Палетни</v-tab>
                            <v-tab disabled class='tab-text secondary--text lighten-2'>Експресни</v-tab> -->
                            </v-tabs>
                        </v-app-bar>
                    </v-card-title>


                    <!-- Domestic Parcels -->
                    <v-form v-show='selectedCategory == 0'
                        ref="form_intro"
                        @submit.prevent=""
                        @submit="checkParcelPrice"
                        v-model="valid_form_intro"
                        lazy-validation
                    >
                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md4>
                                <v-select
                                required
                                :rules='parcelValidationServiceRules'
                                :disabled='canProceedOrder'
                                :readonly="parcelPriceRecieved"
                                v-model="serviceType"
                                :items="typeOfService"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Избери вида на доставката"
                                ></v-select>
                            </v-flex>
                            <v-flex xs10 sm10 md3>
                                <v-select
                                required
                                :readonly="parcelPriceRecieved"
                                :rules='parcelValidationParcelRules'
                                v-model="parcelType"
                                background-color='white darken-3'
                                filled
                                :items="typeOfParcel"
                                :menu-props="{ maxHeight: '200' }"
                                label="Избери вида на пратката"
                                @change="documentPackage"
                                ></v-select>
                            </v-flex>
                            <v-flex xs10 sm10 md3>
                                <v-text-field
                                required
                                :rules="parcelValidationWeightRules"
                                suffix='kg'
                                v-model="weight"
                                background-color='white darken-3'
                                filled
                                :disabled='weightEnabled'
                                :readonly="parcelPriceRecieved"
                                label="Тежест на пратката (в кг.)" 
                                hint="Въведете по-високата стойност от: Tегло или обемно тегло"
                                persistent-hint
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md5>
                                <v-autocomplete
                                no-data-text="Въвели сте несъществуваща локация или използвате латиница."
                                required
                                return-object
                                auto-select-first
                                :loading='officesLoading'
                                :disabled='officesLoading'
                                :readonly="parcelPriceRecieved"
                                :rules='parcelValidationAddressRules'
                                v-model="senderCity"
                                :items="evropatDestinations"
                                :item-text="item => `${item.destinationName} - ${item.destinationProvince} [${item.destinationPostCode}]`"
                                :filter="filterSendRecieve"
                                :menu-props="{ maxHeight: '200' }"
                                background-color='white darken-3'
                                clearable
                                filled
                                label="Изпрати от"
                                persistent-hint
                                >
                                </v-autocomplete>
                            </v-flex>
                            <v-flex xs10 sm10 md5>
                                <v-autocomplete
                                no-data-text="Въвели сте несъществуваща локация или използвате латиница."
                                required
                                return-object
                                auto-select-first
                                :loading='officesLoading'
                                :disabled='officesLoading'
                                :readonly="parcelPriceRecieved"
                                :rules='parcelValidationAddressRules'
                                v-model="recipientCity"
                                :items="evropatDestinations"
                                :item-text="item => `${item.destinationName} - ${item.destinationProvince} [${item.destinationPostCode}]`"
                                :filter="filterSendRecieve"
                                :menu-props="{ maxHeight: '200' }"
                                background-color='white darken-3'
                                clearable
                                filled
                                label="Получи в"
                                persistent-hint
                                ></v-autocomplete>
                            </v-flex>
                        </v-layout>

                        <!-- <v-card-text>
                            Вижте партньорската ни офисна мрежа <a target="_blank" href='https://evropat.bg/offices/'>тук</a>!
                        </v-card-text> -->

                        <v-layout v-if='selectedCategory == 0' row wrap align-center justify-center>
                            <v-flex v-if="!parcelPriceRecieved || canProceedOrder" xs8 sm4 md3>
                                <v-btn
                                block
                                rounded
                                color="primary"
                                type="submit"
                                :loading="response_is_loading"
                                >
                                    Покажи цена</v-btn
                                >
                            </v-flex>
                            <v-flex v-if="parcelPriceRecieved" xs8 sm4 md3>
                                <v-btn
                                block
                                rounded
                                color="primary"
                                @click="clearParcelOrder"
                                >
                                    Направи ново запитване</v-btn
                                >
                            </v-flex>
                        </v-layout>
                            <v-layout v-if='selectedCategory == 0' row wrap align-center justify-center>
                            <v-flex v-if='parcelPriceRecieved' justify-center xs8 sm6 md2>
                                <v-text-field
                                readonly
                                :value='parcelPrice'
                                dense
                                suffix='лв.'
                                class="text-md-h5 font-weight-bold centered-input mt-3"
                                color="primary">
                                </v-text-field>
                            </v-flex>
                            <v-flex v-if='parcelPriceRecieved' xs8 sm10 md2>
                                <v-btn
                                rounded
                                block
                                color="primary"
                                :loading="response_is_loading"
                                :disabled='canProceedOrder'
                                @click="proceedDetails">
                                    Напред
                                </v-btn>
                            </v-flex>
                        </v-layout>

                        <v-expand-x-transition ease-in>
                        <v-layout v-show='is_parcel_outside_schedule' row wrap justify-center>
                            <v-flex xs8 sm10 md10>
                                    <v-alert
                                    type="info"
                                    dismissible
                                    > {{ scheduled_day_delivery }}
                                    </v-alert>
                            </v-flex>
                        </v-layout>
                        </v-expand-x-transition>

                        <v-expand-x-transition ease-in>
                        <v-layout v-if='parcelAlert' row wrap justify-center>
                            <v-flex xs8 sm10 md10>
                                    <v-alert
                                    type="error"
                                    dismissible
                                    > {{ parcelMessage }}
                                    </v-alert>
                            </v-flex>
                        </v-layout>
                        </v-expand-x-transition>

                        <v-card-text color='primary'>
                            Цените са крайни, с вкл. такса за гориво, тол и включено ДДС.<br>
                            <strong>Калкулира се обемно тегло на пратката в случай че надвишава реалното по формула Д*Ш*В/5000</strong><br>
                            Пример: кашон с размери 50х50х50см - обемно тегло 50*50*50=125,000 / 5000 = 25кг <br>
                        </v-card-text>
                    </v-form>

                    <!-- International Parcels -->
                    <v-form v-show='selectedCategory == 1'
                        ref="international_form_intro"
                        @submit.prevent=""
                        @submit="checkParcelInternationalPrice"
                        v-model="international_valid_form_intro"
                        lazy-validation
                    >

                        <!-- Parcel Main Data -->
                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md4>
                                <v-select
                                required
                                :rules='parcelValidationServiceRules'
                                :disabled='canProceedOrder || internationalParcelPriceRecieved'
                                v-model="serviceType_international"
                                :items="typeOfService_international"
                                item-value="value"
                                item-text="description"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Избери вида на услугата"
                                class="primary--hint"
                                :hint="serviceType_international_description"
                                persistent-hint
                                ></v-select>
                            </v-flex>
                            <!-- <v-flex xs10 sm10 md3>
                                <v-select
                                required
                                :rules='parcelValidationServiceRules'
                                :disabled='canProceedOrder'
                                v-model="domesticType_international"
                                :items="typeOfDomesticTransport_international"
                                item-value="value"
                                item-text="description"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Избери начин за изпращане" 
                                ></v-select>
                            </v-flex> -->
                            <v-flex xs10 sm10 md3>
                                <v-text-field
                                required
                                :disabled='canProceedOrder || internationalParcelPriceRecieved'
                                :rules="international_parcelValidationWeightRules"
                                suffix='kg'
                                v-model.number="international_weight"
                                type="number"
                                background-color='white darken-3'
                                filled
                                label="Тежест на пратката (в кг.)" 
                                :hint="internationalWeightRestrict"
                                persistent-hint
                                @input="international_weightValidate"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md3>
                                <v-select
                                :disabled='canProceedOrder || internationalParcelPriceRecieved'
                                v-model="insurance_international"
                                :items="typeOfInsurance_international"
                                item-value="value"
                                item-text="description"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Застраховка на пратката"
                                ></v-select>
                            </v-flex>
                        </v-layout>

                        <!-- Volume calculator -->
                        <v-layout v-if="this.serviceType_international == 'EXP' || this.serviceType_international == 'PRM'" row wrap justify-center>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                required
                                type='number'
                                :rules='international_parcelValidationVolumeRules'
                                v-model="parcel_international_lenght"
                                background-color='white darken-3'
                                filled
                                suffix='cm'
                                label="Дължина"
                                hint="Дължина"
                                persistent-hint
                                @input="international_weightValidate"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                required
                                type='number'
                                :rules="international_parcelValidationVolumeRules"
                                v-model="parcel_international_width"
                                background-color='white darken-3'
                                filled
                                suffix='cm'
                                label="Ширина" 
                                hint="Ширина"
                                persistent-hint
                                @input="international_weightValidate"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md2>
                                <v-text-field
                                required
                                :rules="international_parcelValidationVolumeRules"
                                type='number'
                                v-model="parcel_international_height"
                                background-color='white darken-3'
                                filled
                                suffix='cm'
                                label="Височина"
                                hint="Височина"
                                persistent-hint
                                @input="international_weightValidate"
                                ></v-text-field>
                            </v-flex>
                            <v-flex xs10 sm10 md4>
                                <v-text-field
                                readonly
                                type='number'
                                v-model="calculateParcelVolume"
                                background-color='white darken-3'
                                suffix='kg'
                                filled
                                label="Обемно тегло"
                                @input="international_weightValidate"
                                ></v-text-field>
                            </v-flex>
                        </v-layout>

                        <!-- Destinations -->
                        <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md3>
                                <v-select
                                required
                                :rules='parcelValidationServiceRules'
                                :disabled='canProceedOrder || internationalParcelPriceRecieved'
                                v-model="domesticType_international"
                                :items="typeOfDomesticTransport_international"
                                item-value="value"
                                item-text="description"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Избери начин за изпращане" 
                                ></v-select>
                            </v-flex>
                            <v-flex v-if="domesticType_international == 2 || domesticType_international == ''" xs10 sm10 md3>
                                <v-autocomplete
                                :loading='officesLoading'
                                :disabled='officesLoading || canProceedOrder || internationalParcelPriceRecieved'
                                required
                                :rules='parcelValidationAddressRules'
                                v-model="international_senderOffice"
                                :items="evropatOffices"
                                clearable
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Офис изпращане"
                                persistent-hint
                                ></v-autocomplete>
                            </v-flex>
                            <v-flex v-if="domesticType_international == 2 || domesticType_international == ''" xs10 sm10 md4>
                                <v-select
                                required
                                :disabled='canProceedOrder || internationalParcelPriceRecieved'
                                :rules='parcelValidationParcelRules'
                                :items="international_countries_list"
                                v-model="recipientCountry"
                                item-value="code"
                                item-text="name"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Държава на получателя"
                                ></v-select>
                                <!-- <country-select className='v-select' v-model="country_international" :country="country_international"/> -->
                            </v-flex>
                            <v-flex v-if="domesticType_international == 4"  xs10 sm10 md4>
                                <v-autocomplete
                                no-data-text="Въвели сте несъществуваща локация или използвате латиница."
                                required
                                return-object
                                auto-select-first
                                :loading='officesLoading'
                                :disabled='officesLoading || canProceedOrder || internationalParcelPriceRecieved'
                                :readonly="parcelPriceRecieved"
                                :rules='parcelValidationAddressRules'
                                v-model="senderCity"
                                :items="evropatDestinations"
                                :item-text="item => `${item.destinationName} - ${item.destinationProvince} [${item.destinationPostCode}]`"
                                :filter="filterSendRecieve"
                                :menu-props="{ maxHeight: '200' }"
                                background-color='white darken-3'
                                clearable
                                filled
                                label="Изпрати от"
                                persistent-hint
                                >
                                </v-autocomplete>
                            </v-flex>
                            <v-flex v-if="domesticType_international == 4" xs10 sm10 md3>
                                <v-select
                                required
                                :disabled='canProceedOrder || internationalParcelPriceRecieved'
                                :rules='parcelValidationParcelRules'
                                :items="international_countries_list"
                                v-model="recipientCountry"
                                item-value="code"
                                item-text="name"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Държава на получателя"
                                ></v-select>
                                <!-- <country-select className='v-select' v-model="country_international" :country="country_international"/> -->
                            </v-flex>
                        </v-layout>

                        <v-layout v-if="this.serviceType_international == 'EXP' || this.serviceType_international == 'PRM'" row wrap justify-center>
                                <v-flex xs8 sm10 md10>
                                    <v-checkbox
                                    required
                                    :rules="[v => !!v || 'Трябва да се съгласите с условията на ПРАТКА.БГ за да продължите.']"
                                    color='primary'
                                    style='black--text'
                                    :label="international_weight_warning"
                                    value="red"
                                    hide-details>
                                    </v-checkbox>
                                </v-flex>
                        </v-layout>

                        <v-card-text v-show="domesticType_international == 2">
                            Вижте партньорската ни офисна мрежа <a target="_blank" href='https://evropat.bg/offices/'>тук</a>!
                        </v-card-text>

                        <!-- <v-layout v-if="domesticType_international == 4" row wrap justify-center>
                            <v-flex xs10 sm10 md3>
                                <v-autocomplete
                                :loading='destinationsLoading'
                                :disabled='destinationsLoading'
                                v-model="senderProvince"
                                background-color='white darken-3'
                                clearable
                                filled
                                :items="evropatProvinces"
                                :menu-props="{ maxHeight: '200' }"
                                label="Област на подател"
                                persistent-hint
                                required
                                :rules='parcelValidationAddressRules'
                                ></v-autocomplete>
                            </v-flex>
                            <v-flex xs10 sm10 md4>
                                <v-autocomplete
                                :loading='destinationsLoading'
                                :disabled='destinationsLoading'
                                v-model="senderCity"
                                background-color='white darken-3'
                                clearable
                                filled
                                :items="evropatProvinceCitiesSender"
                                :menu-props="{ maxHeight: '200' }"
                                label="Населено място на подател"
                                persistent-hint
                                required
                                :rules='parcelValidationAddressRules'
                                ></v-autocomplete>
                            </v-flex>
                            <v-flex xs10 sm10 md3>
                                <v-select
                                required
                                :rules='parcelValidationParcelRules'
                                :items="international_countries_list"
                                v-model="recipientCountry"
                                item-value="code"
                                item-text="name"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Държава на получателя"
                                ></v-select>
                            </v-flex>
                        </v-layout> -->

                        <!-- Extra -->
                        <!-- <v-layout row wrap justify-center>
                            <v-flex xs10 sm10 md5>
                                <v-select
                                v-model="insurance_international"
                                :items="typeOfInsurance_international"
                                item-value="value"
                                item-text="description"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Застраховка на пратката"
                                ></v-select>
                            </v-flex>
                            <v-flex xs10 sm10 md5>
                                <v-select
                                required
                                :rules='parcelValidationParcelRules'
                                v-model="priority_international"
                                :items="typeOfPriority_international"
                                item-value="value"
                                item-text="description"
                                background-color='white darken-3'
                                filled
                                :menu-props="{ maxHeight: '200' }"
                                label="Приоритет на пратката"
                                ></v-select>
                            </v-flex>
                        </v-layout> -->

                        <v-slide-x-transition ease-in>
                            <v-layout v-show='international_weight_alert_flag' row wrap justify-center>
                                <v-flex xs8 sm10 md10>
                                    <v-alert
                                    :type="international_weigh_alert_type"
                                    > {{international_weigh_alert_message}}
                                    </v-alert>
                                </v-flex>
                            </v-layout>
                        </v-slide-x-transition>

                        <v-layout v-if='selectedCategory == 1' row wrap align-center justify-center>
                            <v-flex v-if="!internationalParcelPriceRecieved" xs8 sm4 md3>
                                <v-btn
                                block
                                rounded
                                color="primary"
                                type="submit"
                                :disabled='canProceedOrder || !international_weightValidate'
                            >
                                Покажи цена</v-btn
                            >
                            </v-flex>
                            <v-flex v-if="internationalParcelPriceRecieved" xs8 sm4 md3>
                                <v-btn
                                block
                                rounded
                                color="primary"
                                @click="clearParcelOrder"
                                >
                                    Направи ново запитване</v-btn
                                >
                            </v-flex>
                            <v-flex v-if='internationalParcelPriceRecieved' justify-center xs8 sm6 md2>
                                <v-text-field
                                readonly
                                :value='parcelPrice'
                                dense
                                suffix='лв.'
                                class="text-md-h5 font-weight-bold centered-input mt-3"
                                color="primary" >
                                </v-text-field>
                            </v-flex>
                            <v-flex v-if='internationalParcelPriceRecieved' xs8 sm10 md3>
                                <v-btn
                                rounded
                                block
                                color="primary"
                                :loading="response_is_loading"
                                :disabled='canProceedOrder || international_weight_alert_flag'
                                @click="proceedDetailsInternational">
                                    Напред
                                </v-btn>
                            </v-flex>
                        </v-layout>

                        <v-expand-x-transition ease-in>
                        <v-layout v-show='is_parcel_outside_schedule' row wrap justify-center>
                            <v-flex xs8 sm10 md10>
                                    <v-alert
                                    type="info"
                                    dismissible
                                    > {{ scheduled_day_delivery }}
                                    </v-alert>
                            </v-flex>
                        </v-layout>
                        </v-expand-x-transition>

                        <v-expand-x-transition ease-in>
                        <v-layout v-if='parcelAlert' row wrap justify-center>
                            <v-flex xs8 sm10 md10>
                                    <v-alert
                                    type="error"
                                    dismissible
                                    > {{ parcelMessage }}
                                    </v-alert>
                            </v-flex>
                        </v-layout>
                        </v-expand-x-transition>

                    
                        <v-card-text color='primary'>
                            Цените са крайни, с вкл. такса за гориво, тол и включено ДДС.<br>
                            <strong>{{international_selected_service_details}}</strong>
                            <!-- <strong>Калкулира се обемно тегло на пратката в случай че надвишава реалното по формула Д*Ш*В/6000</strong><br>
                            Пример: кашон с размери 50х50х50см - обемно тегло 50*50*50=125,000 / 6000 = 20,83кг <br> -->
                        </v-card-text>
                    </v-form>
                </v-card>
                
                <!-- Parcel Card Selection -->
                <v-card v-show='parcelSelection' elevation="4" align='center' color='secondary'>
                    <v-card-title>
                        <v-app-bar 
                        elevation="4"
                        color="primary"
                        dark
                        rounded
                        align='center' >
                        {{parcelType}} - {{weight}} kg
                        </v-app-bar>
                    </v-card-title>

                    <!-- Courier Price Cards -->
                    <v-layout row wrap justify-center>
                        <v-flex 
                        v-for="item in parcel_price_response"
                        :key="item.service"
                        xs10 sm10 md4>
                            <v-hover
                                v-slot="{ hover }"
                            >
                            <v-card
                            :class="`elevation-${hover ? 24 : 3}`"
                            class="mx-auto pa-6 transition-swing"
                            height="250"
                            align='center' color='secondary'>
                                <v-list-item two-line>
                                    <v-list-item-content>
                                        <div class="text-overline mb-4">
                                        Партньор
                                        </div>
                                        <v-list-item-title class="text-h5 mb-1">
                                            {{getCourierService(item.service)}}
                                        </v-list-item-title>

                                        <v-list-item-subtitle>
                                            <v-text-field
                                            readonly
                                            :value='item.price'
                                            dense
                                            suffix='лв.'
                                            class="text-md-h5 text-xs-h6 font-weight-bold centered-input mt-3"
                                            color="primary">
                                            </v-text-field>
                                        </v-list-item-subtitle>
                                    </v-list-item-content>

                                    <v-list-item-avatar
