    methods: {
        filterSendRecieve(item, queryText) {
            return (
                item.destinationName.toLocaleLowerCase().indexOf(queryText.toLocaleLowerCase()) >
                -1 ||
                item.destinationPostCode.toLocaleLowerCase().indexOf(queryText.toLocaleLowerCase()) > -1
            )
        },

        GetCityOffices(city) {
            if(this.service_provider == 'evropat'){
                let evropatOffices = []
                this.officeDatabase.forEach(function (item) {
                    if ((item.officeName).includes(city)) {
                        evropatOffices.push({'name': item.officeName,
                                            'id': [item.officeID, item.officeDestinationID, item.officeAddress],
                                            })
                    }
                });
                evropatOffices.sort()
                return evropatOffices
            }
            
            else if (this.service_provider == 'econt') {
                let Offices = []
                this.econtOffices.forEach(function (item) {
                    let name = (item.address.city.name).toUpperCase()
                    if (name.includes(city)) {
                        Offices.push({'name': item.name,
                                      'id': [item.code, item.address.city.postCode],
                                    })
                    }
                });
                Offices.sort()
                return Offices
            }
            
            // console.log(indexAddresses)
        },

        checkTime() {
            // let closest
            // let today = new Date()
            // let time = today.getHours() + ":" + today.getMinutes()
            let holderArray = this.pickupTime

            var timeToReturn = new Date();
            timeToReturn.setMilliseconds(Math.round(timeToReturn.getMilliseconds() / 1000) * 1000);
            timeToReturn.setSeconds(Math.round(timeToReturn.getSeconds() / 60) * 60);
            timeToReturn.setMinutes((Math.round(timeToReturn.getMinutes() / 15) * 15) +30);
            let time = timeToReturn.getHours() + ":" + timeToReturn.getMinutes()
            let hour = timeToReturn.getHours()


            if (hour < 17) {
                // closest = this.pickupTime.sort( (a, b) => Math.abs(time - a) - Math.abs(time - b) )[0]
                let indexTime = this.pickupTime.findIndex(object => {
                    return object === time;
                });
                holderArray = holderArray.slice(indexTime+1)
            }
            else {
                // console.log(time)
                this.show_courier_message = true
                this.courier_message = 'Куриерът ще бъде заявен за следващия ден.'
                this.courier_message_type = 'info'
                return holderArray;
            }
            // console.log(holderArray)
            return holderArray
        },
        
        GA_SelectDestination() {
            let destination_type, destination_data;

            if(this.selectedCategory == 0) {
                destination_type = 'domestic'
                destination_data= 'User selected domestic parcel creation'
                Vue.analytics.fbq.event('trackCustom', {
                    content_name: 'DomesticPriceCheck'
                })
            }
            else if(this.selectedCategory == 1) {
                destination_type = 'international'
                destination_data= 'User selected international parcel creation'
                Vue.analytics.fbq.event('trackCustom', {
                    content_name: 'InternationalPriceCheck'
                })
            }
            else if(this.selectedCategory == 2) {
                destination_type = 'palet'
                destination_data= ''

            }
            else if(this.selectedCategory == 3) {
                destination_type = 'express'
                destination_data= ''

            }
            this.$ga.event(destination_type, 'select_category', destination_data, this.selectedCategory)
        },

        GA_CheckPrice() {
            let destination_type, destination_data;

            if(this.selectedCategory == 0) {
                destination_type = 'domestic'
                destination_data= 'User checked price domestic parcel'
                Vue.analytics.fbq.event('ViewContent', {
                    content_name: 'InternationalParcelCreation'
                })
            }
            else if(this.selectedCategory == 1) {
                destination_type = 'international'
                destination_data= 'User checked price international parcel'
                Vue.analytics.fbq.event('ViewContent', {
                    content_name: 'InternationalParcelCreation'
                })
            }
            else if(this.selectedCategory == 2) {
                destination_type = 'palet'
                destination_data= ''

            }
            else if(this.selectedCategory == 3) {
                destination_type = 'express'
                destination_data= ''

            }

            let indexService = this.typeOfService.findIndex(object => {
                    return object === this.serviceType;
                });
            this.$ga.event(destination_type, 'check_price', destination_data, indexService)
        },

        async updateParcel(){
            this.response_is_loading = true
            let is_form_ready = this.$refs.form_finish.validate()
            
            if (is_form_ready == true) {
                this.response_is_loading = false
            let payload = {}
            let deliveryDay = 0

            let indexService = this.typeOfService.findIndex(object => {
                return object === this.serviceType;
            });

            let indexType = this.typeOfParcel.findIndex(object => {
                return object === this.parcelType;
            });
            
            // OtO, OtA, AtO, AtA
            if (indexService == 0){

                payload = {
                    'parcelType': indexType,
                    'parcelWeight': this.weight,
                    'deliveryType': indexService,
                    'sender': {
                        'senderProvince': this.senderCity.destinationProvince,
                        'senderCity': this.senderCity.destinationName,
                        'senderAddress': this.senderAddress
                    },
                    'recipient': {
                        'recipientProvince': this.recipientCity.destinationProvince,
                        'recipientCity': this.recipientCity.destinationName,
                        'recipientAddress': this.recipientAddress
                    },
                    'updatePrice': this.updatePrice,
                    'deliveryDay': deliveryDay,
                }

                this.senderOffice = this.senderCity.destinationName
                this.recipientOffice = this.recipientCity.destinationName
            } else if (indexService == 1){
                payload = {
                    'parcelType': indexType,
                    'parcelWeight': this.weight,
                    'deliveryType': indexService,
                    'sender': {
                        'senderProvince': this.senderCity.destinationProvince,
                        'senderCity': this.senderCity.destinationName,
                        'senderAddress': this.senderAddress
                    },
                    'recipient': {
                        'recipientProvince': this.recipientCity.destinationProvince,
                        'recipientCity': this.recipientCity.destinationName,
                        'recipientAddress': this.recipientAddress
                    },
                    'updatePrice': this.updatePrice,
                    'deliveryDay': deliveryDay,
                }
                this.senderOffice = this.senderCity.destinationName
            } else if (indexService == 2){
                payload = {
                    'parcelType': indexType,
                    'parcelWeight': this.weight,
                    'deliveryType': indexService,
                    'sender': {
                        'senderProvince': this.senderCity.destinationProvince,
                        'senderCity': this.senderCity.destinationName,
                        'senderAddress': this.senderAddress
                    },
                    'recipient': {
                        'recipientProvince': this.recipientCity.destinationProvince,
                        'recipientCity': this.recipientCity.destinationName,
                        'recipientAddress': this.recipientAddress
                    },
                    'updatePrice': this.updatePrice,
                    'deliveryDay': deliveryDay,
                }
                this.recipientOffice = this.recipientCity.destinationName
            } else if (indexService == 3){
                payload = {
                    'parcelType': indexType,
                    'parcelWeight': this.weight,
                    'deliveryType': indexService,
                    'sender': {
                        'senderProvince': this.senderCity.destinationProvince,
                        'senderCity': this.senderCity.destinationName,
                        'senderAddress': this.senderAddress
                    },
                    'recipient': {
                        'recipientProvince': this.recipientCity.destinationProvince,
                        'recipientCity': this.recipientCity.destinationName,
                        'recipientAddress': this.recipientAddress
                    },
                    'updatePrice': this.updatePrice,
                    'deliveryDay': deliveryDay,
                }
            }

            if (this.cashOnDelivery) {
                payload.cashOnDelivery = this.parcel_cashOnDelivery
            }   

            if(this.parcel_returnReceipt){
                payload.hasReturnReceipt = this.parcel_returnReceipt
            }

            if(this.parcel_notificationRecipient){
                payload.notification = this.parcel_notificationRecipient
            }

            if(this.parcel_breakable){
                payload.breakable = this.parcel_breakable
            }

            if(this.parcel_checkTest){
                payload.allowShipmentCheck = this.parcel_checkTest
            }

            if(this.parcel_verifyRecipient){
                payload.verification = this.parcel_verifyRecipient
            }
            
            if(this.service_provider == 'evropat'){
                payload.sender.id = this.senderOfficeID[1]
                payload.recipient.id = this.recipientOfficeID[1]
            }
            else if (this.service_provider == 'econt'){
                payload.sender.id = this.senderOfficeID[0]
                payload.sender.postcode = this.senderOfficeID[1]
                payload.recipient.id = this.recipientOfficeID[0]
                payload.recipient.postcode = this.recipientOfficeID[1]
            }
            
            if (this.cashOnDelivery == false && this.parcel_returnReceipt == false && this.parcel_notificationRecipient == false && this.parcel_breakable == false && this.parcel_checkTest == false && this.parcel_verifyRecipient == false) {
                this.parcelAdditionalServices = 0.00
            }
        
            if(this.service_provider == 'evropat'){
                try {
                    let request = await axios.post('/parcel/evropat/check-price', payload)
                    this.parcelPrice =  Math.abs((parseFloat(request.data.price)).toFixed(2))
                    this.parcelAdditionalServices =  (Math.abs(this.parcelPrice - this.baseParcelPrice)).toFixed(2)
                } catch(e) {
                    console.log(e)
                }
            }
            else if(this.service_provider == 'econt'){
                try {
                    let request_econt = await axios.post('/parcel/econt/check-price', payload)
                    this.parcelPrice =  Math.abs((parseFloat(request_econt.data.price)).toFixed(2))
                    this.parcelAdditionalServices =  (Math.abs(this.parcelPrice - this.baseParcelPrice)).toFixed(2)
                }
                catch(e){
                    console.log(e)
                }
            }
                
            } else {
                this.$vuetify.goTo('#parcelCreation')
                this.cashOnDelivery = false
                this.parcel_returnReceipt = false
                this.parcel_notificationRecipient = false
                this.parcel_breakable = false
                this.parcel_checkTest = false
                this.parcel_verifyRecipient = false
                this.response_is_loading = false
                void(0)
            }
            
        },

        async parcelPriceUpdate() {
            this.updatePrice = true

            if(this.cashOnDelivery){
                // console.log('Cash enabled')
                if(this.parcel_cashOnDelivery > 0)  {
                    await this.sleep(200)
                    this.updateParcel()
                }
            }
            else if(this.parcel_returnReceipt){
                await this.sleep(200)
                this.updateParcel()
            }
            else if(this.parcel_notificationRecipient){
                await this.sleep(200)
                this.updateParcel()
            }
            else if(this.parcel_breakable){
                await this.sleep(200)
                this.updateParcel()
            }
            else if(this.parcel_checkTest){
                await this.sleep(200)
                this.updateParcel()
            }
            else if(this.parcel_verifyRecipient){
                await this.sleep(200)
                this.updateParcel()
            }
            else {
                // console.log('Cash disabled, reverting price')
                this.parcelPrice = this.baseParcelPrice
            }
        },

        async payTester() {
            const form = document.createElement("form");
            const element1 = document.createElement("input"); 
            const element2 = document.createElement("input");
            const element3 = document.createElement("input");    
            const element4 = document.createElement("input");
            const element5 = document.createElement("input");    

            form.method = "POST";
            form.action = "https://www.transcard.bg/pay-gw/vpos";  
            
            element3.value='paylogin';
            element3.name="PAGE";
            form.appendChild(element3);

            element1.value=this.transcard_payment_encoded;
            element1.name="ENCODED";
            form.appendChild(element1);  

            element2.value=this.transcard_payment_checksum;
            element2.name="CHECKSUM";
            form.appendChild(element2);


            element4.value=this.transcard_url_ok;
            element4.name="URL_OK";
            form.appendChild(element4);

            element5.value=this.transcard_url_cancel;
            element5.name="URL_CANCEL";
            form.appendChild(element5);

            document.body.appendChild(form);

            form.submit();
        },

        async orderCourier() {
            this.response_is_loading = true
            this.valid_form = this.$refs.courier_domestic.validate()
            if (this.valid_form) {
                this.isCourierCalled = true
                let payload ={
                    'pickupTimeFrom': this.pickupTimeFrom,
                    'pickupTimeTo': this.pickupTimeTo,
                    'shipmentBarcode': this.selectedWaybill,
                    'destID': this.senderCity.destinationID,
                    'officeID': '',
                    'companyName': this.senderOrganization,
                    'address': this.senderAddress,
                    'addressNumber': this.senderAddressNumber,
                    'personName': this.senderName,
                    'phone': this.senderPhoneCleaned,
                    'phoneRegion': '',
                }
                let response = await axios.post('/parcel/evropat/create-courier-request', payload)
                try {
                    if(response.data.error == null){
                    this.show_courier_message = true
                    this.courier_message = 'Куриерът е успешно заявен.'
                    this.courier_message_type = 'success'
                    }
                }catch(e){
                    this.show_courier_message = true
                    this.courier_message = 'Куриерът е неуспешно заявен.'
                    this.courier_message_type = 'error'
                }
            } else {
                this.response_is_loading = false
                void(0)
            }
            this.response_is_loading = false
        },

        documentPackage() {
            if(this.parcelType=='Документи') {
                this.weight = '0.5'
                this.weightEnabled = true
                this.disableDescription = true
                this.shipmentDescription = 'Документи'
            } else {
                this.weightEnabled = false
                this.weight = '0'
                this.disableDescription = false
                this.shipmentDescription = ''
            }
        },

        async getOffices() {
            try {
                let payload = await axios.get('/parcel/evropat/get-offices')
                let evropatOffices = []
                this.officeDatabase = payload.data
                this.officeDatabase.forEach(function (item) {
                    // console.log(item.officeName, index)
                    evropatOffices.push(item.officeName)
                });
                evropatOffices.sort()
                this.evropatOffices = evropatOffices
                this.officesLoading = false
            } catch (error) {
                console.log(error)
                this.parcelMessage = 'В момента имаме технически проблем или профилактика. Моля да ни извините за неудобството.'
                this.parcelAlert = true
                this.canProceed = true
                // expected output: ReferenceError: nonExistentFunction is not defined
                // Note - error messages will vary depending on browser
            }
        },

        async getDestinations() {
            try {
                let payload = await axios.get('/parcel/evropat/get-destinations')               
                this.evropatDestinations = payload.data
                this.destinationsLoading = false
            } catch (error) {
                this.parcelMessage = 'В момента имаме технически проблем или профилактика. Моля да ни извините за неудобството.'
                this.parcelAlert = true
