        ...mapActions(['proceedParcelDetails']),
        async proceedDetails(item) {
            this.response_is_loading = true
            // this.getOffices()
            function uuidv4() {
                return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
                    (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
                );
                }
            let parcel = {
                'id': uuidv4(),
                'status': 'not_completed',
                'service_provider': this.service_provider,
                'selectedCategory': this.selectedCategory,
                'serviceType': this.serviceType,
                'parcelType': this.parcelType,
                'weight': this.weight,
                'sender': {
                        'senderOffice': this.senderOffice,
                        'senderProvince': this.senderProvince,
                        'senderCity': this.senderCity,
                    },
                'recipient': {
                    'recipientOffice': this.recipientOffice,
                    'recipientProvince': this.recipientProvince,
                    'recipientCity': this.recipientCity,
                },
            }
            // console.log(item)
            this.baseParcelPrice = Math.abs((parseFloat(item.price)).toFixed(2))
            this.parcelPrice =  Math.abs((parseFloat(item.price)).toFixed(2))

            this.service_provider = item.service
            this.serviceType = this.typeOfService[item.delivery_type - 1]

            if(item.service == 'econt') {
                if(item.delivery_type != 4){
                    await this.getEcontOffices()
                }

                if(item.delivery_type == 2) {
                    let city_payload = {
                        "city": this.recipientCity.destinationName
                    }
                    let request = await axios.post('/parcel/econt/get-addresses', city_payload)
                    this.econtRecipientAddresses = request.data
                }
                if(item.delivery_type == 3) {
                    let city_payload = {
                        "city": this.senderCity.destinationName
                    }
                    let request = await axios.post('/parcel/econt/get-addresses', city_payload)
                    this.econtSenderAddresses = request.data
                }
                if(item.delivery_type == 4) {
                    let city_payload = {
                        "city": this.senderCity.destinationName
                    }
                    let request = await axios.post('/parcel/econt/get-addresses', city_payload)
                    this.econtSenderAddresses = request.data

                    let resp_city_payload = {
                        "city": this.recipientCity.destinationName
                    }
                    let resp_request = await axios.post('/parcel/econt/get-addresses', resp_city_payload)
                    this.econtRecipientAddresses = resp_request.data
                }
            }

            else if (item.service == 'evropat' && item.delivery_type != 1) {
                await this.getAddresses()
            }

            await this.proceedParcelDetails(parcel);
            this.parcelIntro = false
            this.parcelSelection = false
            this.parcelDetails = true
            this.$vuetify.goTo('#parcelCreation')
            this.response_is_loading = false
            
        },

