                        url: '/parcel/evropat/get-waybill',
                        method: 'POST',
                        responseType: 'blob',
                        data: payload,
            });
            const blob = new Blob([response.data], { type: 'application/pdf' });
            const objectUrl = window.URL.createObjectURL(blob);
            window.open(objectUrl,'_blank')
            // const link = document.createElement('a');
            // link.href = objectUrl;
            // const filename = 'pratkabg_' + this.selectedWaybill + "_a4" + '.pdf'

            // Download file
            // link.setAttribute('download', filename);
            // document.body.appendChild(link);
            // link.click();
        },

        async shipmentPrintSticker() {

            if(this.service_provider =='evropat'){
                const payload = {
                'shipmentBarcode': this.selectedWaybill,
                'printoutType': 'sticker'
                }
                // console.log(payload)
                let response = await axios({
                            url: '/parcel/evropat/get-waybill',
                            method: 'POST',
                            responseType: 'blob',
                            data: payload,
                });
                const blob = new Blob([response.data], { type: 'application/pdf' });
                const objectUrl = window.URL.createObjectURL(blob);
                window.open(objectUrl,'_blank')
            }
            else if(this.service_provider =='econt'){
                window.open(this.waybillURL,'_blank')
            }
            
            // const link = document.createElement('a');
            // link.href = objectUrl;
            // const filename = 'pratkabg_' + this.selectedWaybill + "_sticker" + '.pdf'

            // Download file
            // link.setAttribute('download', filename);
            // document.body.appendChild(link);
            // link.click();
        },

        shipmentByHand() {
            this.instructionsWaybill = true
        },

        async getPaymentData() {
            try {
                const payload = {
                    'payment_price': this.parcelPrice,
                    'payment_description': 'PRATKABG.COM'
                }
                let response = await axios.post('/payment/transcard/create', payload)
                console.log(response)
                this.transcard_payment_encoded = response.data.tcf_data.payload.ENCODED
                this.transcard_payment_checksum = response.data.tcf_data.payload.CHECKSUM
                this.transcard_can_pay = false
                this.payTester()
                
            } catch (error) {
                this.parcelMessage = 'В момента имаме технически проблем или профилактика. Моля да ни извините за неудобството.'
            }
        },

        sleep(ms) {
            return new Promise((resolve) => setTimeout(resolve, ms));
        },

        async isPaymentDone() {
            if (this.awaitingPaymentConfirmation === true) {
                await this.sleep(3000);
                this.checkPaymentStatus();
            }
        },

        async checkPaymentStatus() {
        },

        international_weightValidate() {
            if (this.serviceType_international == 'LTR') {
                // console.log(this.international_weight <= 2)
                this.international_weigh_alert_type='error'
                this.international_weigh_alert_message='Надвишаваш максималното допустимо тегло за избраната услуга'
                this.international_weight_alert_flag = (this.international_get_greater_weight > 2)
            }
            else if (this.serviceType_international == 'TRC') {
                // console.log(this.international_weight <= 2)
                this.international_weigh_alert_type='error'
                this.international_weigh_alert_message='Надвишаваш максималното допустимо тегло за избраната услуга'
                this.international_weight_alert_flag = (this.international_get_greater_weight > 2)
            }
            else if (this.serviceType_international == 'PRM') {
                // console.log(this.international_weight <= 20)
                this.international_weigh_alert_type='error'
                this.international_weigh_alert_message='Надвишаваш максималното допустимо тегло за избраната услуга'
                this.international_weight_alert_flag = (this.international_get_greater_weight > 20)
            }
            else if (this.serviceType_international == 'EXP') {
                // console.log(this.international_weight <= 15)
                this.international_weigh_alert_type ='error'
                this.international_weigh_alert_message ='Надвишаваш максималното допустимо тегло за избраната услуга'
                this.international_weight_alert_flag = (this.international_get_greater_weight > 15)
            }
            else {
                this.international_weigh_alert_type ='info'
                this.international_weigh_alert_message ='Нямате избрана услуга'
                this.international_weight_alert_flag = (this.international_get_greater_weight >= 0)
            }
        },

    },

    mounted() {
        Vue.analytics.fbq.event('ViewContent', {
            content_name: 'ParcelCreation'
        })
        this.getDestinations()
        this.getOffices()
        // this.getAddresses()

        // Development
